import './setPublicPath'
import Vue from 'vue'
import wrap from '@vue/web-component-wrapper'

// runtime shared by every component chunk
import 'css-loader/lib/css-base'
import 'vue-style-loader/lib/addStylesShadow'
import 'vue-loader/lib/runtime/componentNormalizer'

import barCjs from '~root/src/bar-cjs.js?shadow'
window.customElements.define('basis-bar-cjs', wrap(Vue, barCjs))

import foo from '~root/src/foo.js?shadow'
window.customElements.define('basis-foo', wrap(Vue, foo))

import index from '~root/src/index.js?shadow'
window.customElements.define('basis-index', wrap(Vue, index))

import tambur from '~root/src/tambur.vue?shadow'
window.customElements.define('basis-tambur', wrap(Vue, tambur))